'use client'
import React, { useState, useEffect } from "react";
import Image from "next/image";
import { useParams } from "next/navigation";
import SkeletonNotificationSettings from "@/app/components/ui/SkeletonNotificationSettings";
import AdCard from "@/app/components/ui/AdCard";
import placeholderPost from "../../../../public/resourses/placeholderPost.svg";
import userAvatar from "../../../../public/resourses/userAvatar.svg";
import { Eye, MapPin } from "lucide-react";
import { useGetPublicProfileInfo } from "@/app/lib/postServices/postQueries";
import type { Ad } from "@/app/types/authTypes";

const PublicStore = () => {
  const params = useParams();
  const username = params.username as string;
  const pageSize = 20;

  const [page, setPage] = useState(1);
  const [allPosts, setAllPosts] = useState<any[]>([]);
  const [hasMore, setHasMore] = useState(true);
  const [user, setUser] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(false);

  // جلب البيانات عند تغيير الصفحة
  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      // استدعاء دالة الجلب من الخدمات مباشرة
      const res = await fetch(
        `/api/public-profile?username=${username}&page=${page}&page_size=${pageSize}`
      );
      const data = await res.json();
      if (page === 1) {
        setAllPosts(data.results.posts);
      } else {
        setAllPosts((prev) => [...prev, ...data.results.posts]);
      }
      setUser(data.results.user);
      setHasMore(!!data.next);
      setIsLoading(false);
    };
    fetchData();
  }, [page, username]);

  // تحويل بيانات post القادمة من API إلى نوع Ad المطلوب
  const mappedAds: Ad[] = allPosts.map((post) => ({
    id: post.id,
    offer_type: post.offer_type,
    title: post.title,
    description: post.description,
    city: post.city,
    price: post.price,
    status: "accepted", // أو أي حالة افتراضية مناسبة
    cover_image: post.cover_image ?? placeholderPost,
    subcategory: post.subcategory,
    user_first_name: user?.first_name || "",
    user_last_name: user?.last_name || "",
    user_city: user?.city || "",
    user_profile_image: user?.profile_image || "",
    username: "", // غير متوفر في بيانات post
  }));

  if (isLoading && page === 1) return <SkeletonNotificationSettings />;

  return (
    <div className="w-full flex flex-col justify-between items-start px-8 py-6 gap-8 rtl">
      {/* ... معلومات المستخدم ... */}
      <div className="w-3/4">
        {mappedAds.length > 0 && (
          <div className="h-full rounded-md max-sm:mt-2 mb-10">
            <div className="grid gap-4 grid-cols-2 md:grid-cols-4">
              {mappedAds.map((ad) => (
                <AdCard
                  key={ad.id}
                  title={ad.title}
                  description={ad.description}
                  city={ad.city}
                  price={parseFloat(ad.price)}
                  published={ad.status}
                  isFav={true}
                  imageUrl={ad.cover_image}
                  id={ad.id}
                  subcat={ad.subcategory}
                  offer_type={ad.offer_type}
                />
              ))}
            </div>
            {/* زر تحميل المزيد */}
            {hasMore && (
              <div className="flex justify-center mt-5">
                <button
                  onClick={() => setPage((p) => p + 1)}
                  disabled={isLoading}
                  className="px-6 py-2 rounded-full border bg-white text-gray-600 hover:bg-gray-100 font-semibold shadow"
                >
                  {isLoading ? "...جاري التحميل" : "تحميل المزيد"}
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default PublicStore; 